<template>
  <view class="container">
    <view class="top_container">
      <navBar
        :navColor="navColor"
        :isFixed="false"
        titleColor="#323233"
        title="ÂÅ•Ë∫´Âú∞Âõæ"
        :showBack="true"
        searchTitle=""
      ></navBar>
      <scrollTabX :list="tabList" @click="changeTab"></scrollTabX>
    </view>
    <map
      id="map"
      class="map"
      :latitude="latitude"
      :longitude="longitude"
      :scale="scale"
      :min-scale="1"
      :max-scale="20"
      :enable-overlooking="overlooking"
      :skew="skew"
      @labeltap="clickMarker"
      @markertap="clickMarker"
      v-if="showMapFlag"
    >
      <view class="right_box">
        <view class="right_layer" @click="handleRight(1)">
          <image src="../../static/images/map-layer.png" mode="" class="img"></image>
        </view>
        <view class="right_location" @click="handleRight(3)">
          <image src="../../static/images/map-list.png" mode="" class="img"></image>
        </view>
      </view>
    </map>
    <show-map-info-popup ref="showMapInfoPopupRef" :mapInfo="mapInfo"></show-map-info-popup>
    <mapTouch>
      <template v-slot:header>
        <mapTouchTitleVue :title="mapTouchTitle" :total="siteTotal" />
      </template>
      <template v-slot:body>
        <view>
          <view v-for="(item, index) in mapList" :key="index">
            <instructorSiteItem :info="item" :show-join="false" :page-show-quit="false" />
          </view>
          <empty v-if="mapList.length == 0" />
        </view>
      </template>
    </mapTouch>
  </view>
</template>

<script>
import { getInstructorSiteList } from '@/apis/instructorSite/index'
import { getShortDistanceBase } from '@/utils/mapUtils.ts'
import mapTouchTitleVue from './components/mapTouchTitle.vue'
import instructorSiteItem from '@/components/InstructorSiteItem/index.vue'
import mapTouch from '@/components/mapTouch.vue'
import mapAll from '@/static/images/site-icon.png'
import showMapInfoPopup from './components/showMapInfoPopup.vue'
import { getMapLocation } from '@/utils/index'
import shareMixins from '@/mixins/shareMixins'
import navBar from '@/components/navBar/index.vue'
import scrollTabX from './components/scrollTabX.vue'
export default {
  mixins: [shareMixins],
  components: {
    showMapInfoPopup,
    mapTouch,
    mapTouchTitleVue,
    instructorSiteItem,
    navBar,
    scrollTabX,
  },
  data() {
    return {
      navColor: 'transparent',
      currentGatherTypeId: 2,
      mapAll,
      tabList: [
        {
          name: 'ÊåáÂØºÂëòÁ´ôÁÇπ',
          img: 'https://api.wesais.com/images/924/20250520/5494d965b2aa055d1aa2a986845800cd.png',
          gather_type_id: '',
        },
        {
          name: '‰ΩìËÇ≤Âú∫È¶Ü',
          img: 'https://api.wesais.com/images/924/20250520/2b58e3ea71e86d1003b37e849792a49a.png',
        },
        {
          name: '‰ΩìËÇ≤ÁªÑÁªá',
          img: 'https://api.wesais.com/images/924/20250520/0fca8914a4cca54947a313410d6bf9ba.png',
        },
      ],
      longitude: 115.681239, // ÁªèÂ∫¶
      latitude: 29.67599, // Á∫¨Â∫¶
      minScale: 8,
      maxScale: 20,
      scale: 16,
      oldClientY: 0,
      height: 200,
      overlooking: false, //ÊòØÂê¶ÂºÄÂêØ‰øØËßÜ
      skew: 0,
      sizer: {
        street_id: '',
        gather_type: '',
        search_tag: '',
        community_ids: '',
        keyword: '',
      },
      form: {
        page: 1,
        size: 999,
      },
      center: {
        longitude: 115.681239, // ÁªèÂ∫¶
        latitude: 29.67599, // Á∫¨Â∫¶
      },
      drag_location: {
        longitude: 115.681239, // ÁªèÂ∫¶
        latitude: 29.67599, // Á∫¨Â∫¶
      },
      keyword: '',
      mapList: [],
      siteTotal: 0,
      mapTouchTitle: 'Á´ôÁÇπ',
      _mapContext: null,
      intervalId: '',
      showBack: false,
      isNotHuBei: false,
      oldScale: '16',
      radium: 0,
      markersList: [],
      siteInfoList: [],
      mapInfo: {},
      showMapFlag: false,
    }
  },
  async created(options) {
    await this.setCenterLocation()
    this.showMapFlag = true
    const app = getApp()
    let _this = this
    this._mapContext = uni.createMapContext('map', this)
    this._mapContext.getCenterLocation({
      success: function (res) {
        console.log('Ëé∑ÂèñÂΩìÂâçÁªèÁ∫¨Â∫¶Âú∞ÂùÄ', res)
        if (res.latitude == 0 || res.longitude == 0) {
          _this.latitude = _this.center.latitude
          _this.longitude = _this.center.longitude
          setTimeout(() => {
            _this.setMapCener(_this.latitude, _this.longitude)
          }, 300)
        }
      },
    })

    this._mapContext = uni.createMapContext('map', this)
    console.log('user_latitude', app.globalData.user_latitude)
    if (app.globalData.user_latitude && app.globalData.user_longitude) {
      let user_longitude = app.globalData.user_longitude
      let user_latitude = app.globalData.user_latitude
      this.drag_location.latitude = user_latitude
      this.drag_location.longitude = user_longitude
      // reverseGeocoder(user_latitude, user_longitude).then((res) => {
      //   console.log('res', res)
      //   if (res.province.indexOf('ÊπñÂåó') == -1) {
      //     this.showBack = true
      //     this.isNotHuBei = true
      //     this._mapContext.getCenterLocation({
      //       success: function (res) {
      //         if (res.latitude == 0 || res.longitude == 0) {
      //           _this.latitude = 35.03799
      //           _this.longitude = 111.037283
      //         }
      //         setTimeout(() => {
      //           _this.setMapCener(_this.latitude, _this.longitude)
      //         }, 300)
      //       },
      //     })
      //   } else {
      //     this.latitude = user_latitude
      //     this.longitude = user_longitude
      //   }
      // })
    }

    this._mapContext.initMarkerCluster({
      enableDefaultStyle: false,
      zoomOnClick: true,
      gridSize: 60,
      complete(res) {
        // console.log("initMarkerCluster", res);
      },
    })
    this._mapContext.on('markerClusterCreate', (res) => {
      const clusters = res.clusters
      const markers = clusters.map((cluster) => {
        const { center, clusterId, markerIds } = cluster
        return {
          ...center,
          width: 0,
          height: 0,
          clusterId, // ÂøÖÈ°ª
          label: {
            content: markerIds.length + '',
            fontSize: 20,
            width: 60,
            height: 60,
            bgColor: '#e05830',
            color: '#fff',
            borderRadius: 30,
            textAlign: 'center',
            anchorX: 0,
            anchorY: -30,
          },
        }
      })
      this._mapContext.addMarkers({
        markers,
        clear: false,
        complete(res) {
          console.log('addMarkers', res)
        },
      })
    })
    this.getMapScale()
    await this.getMaqList(this.center.latitude, this.center.longitude)
  },
  destroyed() {
    uni.removeStorageSync('currentGatherType')
  },
  methods: {
    setCenterLocation() {
      return new Promise((resolve) => {
        let { lat, lng } = getMapLocation()
        console.log('lat', lat, lng)
        if (lat && lng) {
          this.latitude = lat
          this.longitude = lng
          this.center = {
            latitude: lat,
            longitude: lng,
          }
          this.drag_location = {
            latitude: lat,
            longitude: lng,
          }
        }
        setTimeout(() => {
          resolve(true)
        }, 20)
      })
    },
    // Ëé∑ÂèñÂàóË°®Êï∞ÊçÆ
    getMaqList(latitude, longitude) {
      if (!latitude || !longitude) {
        latitude = this.drag_location.latitude
        longitude = this.drag_location.longitude
      }
      return new Promise(async (resolve) => {
        let params = {
          ...this.form,
          longitude: longitude,
          latitude: latitude,
          page: 1,
          size: 999,
        }
        let fun = getInstructorSiteList
        this.$nextTick(() => {
          this.siteTotal = 0
          this.mapList = []
          this.renderMarker(this.mapList)
        })
        const res = await fun(params)
        if (res.code !== 200) {
          this.$toast(res.message)
        }
        const data = res.data.listData ? res.data.listData : res.data.list
        data.map((e) => {
          e.icon = mapAll
        })
        this.$nextTick(() => {
          this.siteTotal = res.data.total ? res.data.total : res.data.count
          this.mapList = data
          this.renderMarker(this.mapList)
        })
        resolve(1)
      })
    },
    renderMarker(markers) {
      if (!this._mapContext) {
        console.error('_mapContext Ëé∑ÂèñÂ§±Ë¥•')
        return
      }
      if (markers.length === 0) {
      }
      const showMarkerList = []
      let length = markers.length
      markers.forEach((item, index) => {
        let obj = {
          id: index + 1,
          iconPath: item.icon,
          width: 33,
          height: 40,
          joinCluster: false,
          latitude: parseFloat(item.lat),
          longitude: parseFloat(item.lng),
          level: 1,
          instructor_site_id: item.instructor_site_id,
        }
        showMarkerList.push(obj)
      })
      let _this = this
      console.log('showMarkerList', showMarkerList)

      this._mapContext.addMarkers({
        markers: showMarkerList,
        clear: true,
        complete(res) {
          _this.markersList = showMarkerList
          _this.siteInfoList = markers
        },
        fail(error) {
          console.log('error', error)
        },
        success(res) {
          console.log('res', res)
        },
      })
    },
    clickMarker(e) {
      let markerId = e.markerId
      let item = this.markersList[markerId - 1]
      let _this = this
      if (item.level === 3) {
        this._mapContext.moveToLocation({
          longitude: item.longitude,
          latitude: item.latitude,
          success(res) {
            _this.scale = 12
            _this.drag_location.latitude = _this.center.latitude
            _this.drag_location.longitude = _this.center.longitude
          },
        })
      }
      if (item.level === 4) {
        this._mapContext.moveToLocation({
          longitude: item.longitude,
          latitude: item.latitude,
          success(res) {
            _this.scale = 14
            _this.drag_location.latitude = _this.center.latitude
            _this.drag_location.longitude = _this.center.longitude
          },
        })
      }
      if (item.level === 1) {
        let siteInfo = this.siteInfoList[markerId - 1]
        siteInfo.showImage = siteInfo.img
        _this.mapInfo = siteInfo
        console.log('üöÄ ~ clickMarker ~ siteInfo:', siteInfo)

        _this.$nextTick(() => {
          _this.$refs['showMapInfoPopupRef'].open()
        })
      }
    },
    getMapScale() {
      if (!this._mapContext) {
        console.error('_mapContext Ëé∑ÂèñÂ§±Ë¥•')
        return
      }
      let _this = this
      this.intervalId = setInterval(() => {
        this._mapContext.getScale({
          success: (res) => {
            // 8-11  ÊòæÁ§∫Âå∫
            // 11-14  ÊòæÁ§∫Ë°óÈÅì
            if (this.oldScale != res.scale) {
              this.oldScale = res.scale
            }
          },
        })
      }, 1000)
    },
    regionchange(res) {
      if (res.type === 'end') {
        let new_latitude = res.target.centerLocation.latitude
        let new_longitude = res.target.centerLocation.longitude
        let distance = getShortDistanceBase(
          new_latitude,
          new_longitude,
          this.drag_location.latitude,
          this.drag_location.longitude,
        )
        this.drag_location = {
          latitude: new_latitude,
          longitude: new_longitude,
        }
        if (distance > 100) {
          let scale = this.oldScale
          this.getMaqList(new_latitude, new_longitude)
        }
      }
    },
    // ÁÇπÂáªÂè≥Ëæπ  1 ÂõæÂ±ÇÂàáÊç¢  2 ÂΩìÂâçÂÆö‰Ωç  3 ÂàóË°®
    handleRight(type) {
      if (type == 1) {
        this.overlooking = !this.overlooking
        this.skew = this.overlooking ? 90 : 0
      }
      if (type == 2) {
        let app = getApp()
        if (
          app.globalData.is_get_location &&
          app.globalData.user_latitude &&
          app.globalData.user_longitude
        ) {
          let user_longitude = app.globalData.user_longitude
          let user_latitude = app.globalData.user_latitude
          this.latitude = user_latitude
          this.longitude = user_longitude
          this.setMapCener(user_latitude, user_longitude)
          if (this.isNotHuBei) {
            this.showBack = true
          }
          console.log('showBack', this.showBack)
        } else {
          app.getLocation()
        }
      }
      if (type == 3) {
        uni.navigateTo({ url: '/pages-sub/instructorSite/index' })
      }
    },
    setMapCener(latitude, longitude) {
      if (!this._mapContext) {
        console.error('_mapContext Ëé∑ÂèñÂ§±Ë¥•')
        return
      }
      let _this = this
      this._mapContext.moveToLocation({
        longitude: longitude,
        latitude: latitude,
        success(res) {
          console.log('ËÆæÁΩÆ‰∏≠ÂøÉÁÇπ', res)
          _this.drag_location.latitude = latitude
          _this.drag_location.longitude = longitude
        },
        fail(err) {
          console.log('ËÆæÁΩÆ‰∏≠ÂøÉÁÇπerr', err)
        },
      })
    },
    // ÂõûÂà∞ÊπñÂåó
    handleBack() {
      this.setMapCener(this.center.latitude, this.center.longitude)
      if (this.showBack) {
        this.showBack = false
      }
    },
    getSearchData(data) {
      this.getMaqList()
    },
  },
}
</script>

